button {
    color: aqua;
    font-size: 2em;
    padding: 0.4em;
    border: 2px solid white;
    border-radius: 6%;
    margin: 2%;
    background: rgb(3, 16, 93);
    cursor: pointer;
}

button:hover {
    background-color: aqua;
    color: black;
}

.input1 {
    vertical-align: bottom;
    text-align: right;
    color: aqua;
    font-size: 150%;
    height: 3em;
    width: 90%; 
    background: rgb(19, 19, 154);
    border-radius: 5% 5% 0 0;
    border: none;
}

.input2 {
    vertical-align: bottom;
    text-align: right;
    color: aqua;
    font-size: 150%;
    height: 2em;
    width: 90%; 
    background: rgb(19, 19, 154);
    border-radius: none;
    border: none;
    margin-bottom: 4%;
}

.container {
    width: 17em;
    padding: 1.5em;
    margin-left: 36%;
    border: 6px solid rgb(13, 5, 243);
    background: rgb(3, 16, 93);
    border-radius: 10%;
}

.keypad {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
}